@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@1,200&display=swap);
header {
  font-family: "Montserrat", sans-serif;
}

header h1 {
  color: #c8bc9c;
  font-weight:lighter;
  font-size:4ex;
  font-family:"Great Vibes";
}

header a {
  color: #000 !important;
  font-size: 1rem;
  font-weight: bold;
}

.navbar {
  background-color: #dbe2ef;
  margin: auto -16px;
  padding: 5px auto;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  padding-bottom: 0 0 4.5rem;
}

.navbar a:hover,
:focus {
  color: burlywood !important;
}

.colorChange {
  background-color: #112d4e;
}

.colorChange h1 {
  color: burlywood;
}

.colorChange a {
  color: burlywood !important;
  text-decoration: none;
}

.colorChange a:hover {
  color: yellow !important;
}

.home {
  background-image:url(/vasukiraj01.github.io/static/media/background.573932f7.png);
  background-color: #cccccc;
  height: 900px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  /* background-image:linear-gradient(to bottom,black,black,grey); */
  text-align: left;
  padding: 5% 5%;
  color: #cdcbd6;
}

.home h1,
h2,
h3,
p {
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  color: #544431;
  
}
.home-col {
  padding: 15% 5%;
}
.button-home {
  margin: 5% 3% 5% 0%;
}

.button-home a:hover,
a:focus {
  color: #9acd32 !important;
}

.display {
  -webkit-text-decoration: 3px underline;
          text-decoration: 3px underline;
  -webkit-text-decoration-color: #c8bc9c;
          text-decoration-color: #c8bc9c;
  font-size: large;
}

.image-home {
  width: 80%;
  padding-left: 10%;
  padding-top: 30%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  position: absolute;
}

@media (max-width: 1028px) {
  .home {
    text-align: center;
  }

  .image-home {
    position: static;
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
}

.about {
  background-color: #F7F1ED;
  /*background-image:linear-gradient(to bottom,#222831,#393e46,#00adb5);*/
  color: black;
}

.about h1,
h2,
h3,
h6
{
  font-family: 'Playfair Display', serif;
}
.about p{
  font-family: 'Barlow', serif;
  font-size: 18px;
  font-weight:600;
}

.about h1 {
  text-align: center;
  font-weight: bold;
}

.about h6 {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: large;
}

.about h1:before {
  content: "\300E   ";
}

.about h1:after {
  content: " \300F";
}

.about-row {
  padding: 2%;
}

.about-col {
  padding: auto 10%;
}
.about-image {
  width: 80%;
}

.education {
  background-image:linear-gradient(to bottom,#71c9ce,#a6e3e9,#cbf1f5);
  font-family: "Montserrat", sans-serif;
}
.education h1 {
  text-align: center;
  font-weight: bold;
  color: #e3fdfd;
}

.education h1:before {
  content: "\300E   ";
}

.education h1:after {
  content: " \300F";
}

.education h3,
h5 {
  color: #000;
  font-weight: bold;
}

.education h6 {
  text-align: center;
  padding-bottom: 2%;
  color: #008289;
  font-weight:100;
  font-family: "Montserrat", sans-serif;
  font-size: large;
}

.education-date {
  color: #000;
  font-weight: bold !important;
}

.skill {
  background-color: #dbe2ef;
  font-family: "Montserrat", sans-serif;
  text-align: center;
}
.skill h1 {
  font-weight: bold;
}

.skill h1:before {
  content: "\300E   ";
}

.skill h1:after {
  content: " \300F";
}

.skill-card {
  margin: 20px auto;
  width: 20rem;
}

.skill h6{
  font-size: large;
  font-family: "Montserrat", sans-serif;
}

.project {
  background-color: #3f72af;
  font-family: "Montserrat", sans-serif;
}

.project h1 {
  text-align: center;
  font-weight: bold;
  color: #f9f7f7;
}

.project h1:before {
  content: "\300E   ";
}

.project h1:after {
  content: " \300F";
}

.project h6 {
  text-align: center;
  padding-bottom: 2%;
  color: #f9f7f7;
}

.project-labeltitle {
  color: #000;
  font-weight: bold !important;
  margin: 0 auto !important;
}

.project-subtitle {
  color: #282829;
  font-weight: bolder !important;
  margin: 0 auto !important;
}

.project-detail {
  color: #000;
  font-weight: bold !important;
}

.project-button {
  margin: 4% 1% auto;
}

.contact {
  background-color: #dbe2ef;
  color: #000;
  text-align: center;
}

.contact h1,
h2,
h3,
h5,
p {
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
}

.contact h6{
  font-size:medium;
}

.contact h5{
  font-size:large;

}

.contact h1:before {
  content: "\300E   ";
}

.contact h1:after {
  content: " \300F";
}

.contact h3 {
  color: #000;
}

.contact a {
  color: #000;
}

.contact a:hover {
  color: #28b0fa !important;
}

.contact a:focus {
  color: #000 !important;
}

.contact-row {
  margin: 2% 10%;
}

.contact-col {
  margin: 5% auto;
}

.contact-a {
  margin: auto 1%;
}

.contact-img {
  width: 400px;
  height: 380px;
}

@media (max-width: 1028px) {
  .contact-carouse {
    width: auto;
    height: auto;
  }
}

footer {
  font-family: "Montserrat", sans-serif;
  background-color: #112d4e;
  text-align: center;
  padding: 0.2rem;
  bottom: 0;
  width: 100%;
}

footer p {
  color: #fff;
  margin: 10px auto;
}

footer p span {
  color: #c0fefc;
}


